import React, { useEffect } from "react";
import Chart from "chart.js/auto";
import "./LineChart.css";

// Assuming 'sun' and 'cloud' are created before with `new Image()`
var sun = new Image();
sun.src = 'https://i.imgur.com/yDYW1I7.png';

function LineChart({ chartData }) {
    useEffect(() => {
        const ctx = document.getElementById("myChart").getContext("2d");

        const totalDuration = 9000;
        const delayBetweenPoints = totalDuration / chartData.labels.length;

        // const taylorSwift = new Image();
        // taylorSwift.src = './Taylor_Swift.jpg';
        //
        // taylorSwift.onload = function () {
        //     console.log("Image loaded successfully");
        // };
        //
        // taylorSwift.onerror = function (error) {
        //     console.error("Error loading image:", error);
        // };

        const lineChart = new Chart(ctx, {
            type: "line",
            data: chartData,
            options: {
                scales: {
                    y: {
                        reverse: true,
                        max: 6,
                        min: 0,
                        ticks: {
                            stepSize: 1,
                            callback: function (value, index, values) {
                                // Hide the label for 0
                                return value === 0 ? "" : value;
                            },
                        },
                    },
                },
                layout: {
                    padding: {
                        top: 20,
                    },
                },
                animation: {
                    x: {
                        type: "number",
                        easing: "linear",
                        duration: delayBetweenPoints,
                        from: NaN,
                        delay(ctx) {
                            if (ctx.type !== "data" || ctx.xStarted) {
                                return 0;
                                console.log("hi");
                            }
                            ctx.xStarted = true;
                            return ctx.index * delayBetweenPoints;
                        },
                    },
                    y: {
                        type: "number",
                        easing: "linear",
                        duration: delayBetweenPoints,
                        from: (ctx) =>
                            ctx.index === 0
                                ? ctx.chart.scales.y.getPixelForValue(6)
                                : ctx.chart.getDatasetMeta(ctx.datasetIndex).data[ctx.index - 1].getProps(["y"], true).y,
                        delay(ctx) {
                            if (ctx.type !== "data" || ctx.yStarted) {
                                return 0;
                            }
                            ctx.yStarted = true;
                            return ctx.index * delayBetweenPoints;
                        },
                    },
                    onComplete: () => {
                        const datasetIndex = 4;
                        const dataIndex = 10; // Change this index to the desired data point
                        const point = lineChart.getDatasetMeta(datasetIndex).data[dataIndex];

                        if (point) {
                            const taylorSwiftImage = new Image();
                            taylorSwiftImage.src = 'https://i.imgur.com/CtXIEj7.png'
                            taylorSwiftImage.onload = () => {
                                ctx.drawImage(
                                    taylorSwiftImage,
                                    point.x - taylorSwiftImage.width / 2,
                                    point.y - taylorSwiftImage.height / 2,
                                    taylorSwiftImage.width,
                                    taylorSwiftImage.height
                                );
                            };
                        }
                    },
                },

                },
        });

        return () => {
            console.log("Image0");
            lineChart.destroy();
        };
    }, [chartData]);

    return (
        <div className="chartWrapper">
            <div className="chartAreaWrapper">
                <canvas id="myChart" height="500" width="1200"></canvas>
            </div>
        </div>
    );
}

export default LineChart;
